import React, { useState } from 'react';
import Button from '@material-ui/core/Button';
import AddBoxIcon from '@material-ui/icons/AddBox';

const Createnote = (props) => {
  const [expand, setexpand] = useState(false);
  const [note, setnote] = useState({
    title: '',
    content: '',
  });

  const InputEvent = (event) => {
    const { name, value } = event.target
    setnote((prevdata) => {
      return {
        ...prevdata,
        [name]: value,
      }
    })
    console.log(note);
  };

  const addEvent = () => {
    props.passNote(note);
    setnote({
      title: '',
      content: '',
    });
  };

  const expandit = () => {
    setexpand(true);
  };
  const backtonormal = () => {
    setexpand(false);
  };


  return (<>
    <div className='main_note' onDoubleClick={backtonormal}>
      <form>
        {expand ?
          <input type='text' value={note.title} name='title' onChange={InputEvent} placeholder='Title' autoComplete='off'></input> : null}
        <textarea value={note.content} name='content' onChange={InputEvent} placeholder='Write a note...' rows='' column='' onClick={expandit}  >
        </textarea>
        {expand ?
          <Button onClick={addEvent}>
            <AddBoxIcon className='plus_sign' />
          </Button> : null}
      </form>
    </div>
  </>)
};

export default Createnote;